<template>
  <div id="app">
    <img src="./assets/logo.png">
    <router-view></router-view>
   <test000 toutou="qita" name="haha" @hi='gohi'><!-- //接收toutou --><!-- 监听内部hi事件 监听到hi事件就执行gohi事件 -->
    <h1>adfhaifha</h1>
   </test000>
  </div>
</template>

<script>
import test from '~/test1'
export default {
  name: 'app',
  components:{            //注册test标签
    test000 : test
  },
  methods:{
    gohi(data){   // 声明gohi事件
      alert('我触发了hi')
      console.log(data)
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

</style>



