<template>
	<div class="test">
		 <!-- <hello></hello> -->
	   <h1>{{ title }}</h1>
	   <h1 v-text="title"></h1>
	   <h1 v-html="title"></h1>
	   <input type="text" v-model="title" name="">
	   <p v-for="(value,index) in nav" 
	    v-bind:title="value" 
	    v-bind:key="value"
	    :class="{ on: index == curIndex }"
	    v-on:click="show(index)"
	   >{{index}}-{{value}}</p>
	   <p :style="{'margin-left':dbCurNum}">haha</p>
	   <p>{{dbCurNum}}</p>
	   <!-- ：class=" { } "  条件符合加入class -->
	   <!-- v-on:触发事件 == @触发事件 -->
	   <button @click="isshow = !isshow">toggle</button>
	    <p v-show="isshow">show</p>
	    <p v-if="isshow">if</p>
	    <p v-else-if="!isshow">ss</p>
	    <p v-else="isshow">else</p>
	</div>
</template>

<script>

export default{
	data(){//所有数据定义
    return {
      title:'<h2>哎。。。。。<h2>',
      name:'44',
      nav:['women','chanpin','fnren'],
      curIndex: 1 ,
      isshow:true
    }
  },
  components: {
    // hello:a
  },
  computed : {//计算属性
    dbCurNum () {
      return this.curIndex * 100 +"px"
    }
  },
  methods : {//所有方法写在这
    show (index) {
      this.curIndex = index
    }
  }
}
</script>

<style scoped>
	.on{
  font-size: 50px;
}
</style>


