<template>
	<div class="test">
	<slot></slot> <!--  app  test标签内部包裹的内容插入位置 -->
	{{ toutou }}
		<input type="text" name="" @keydown.enter ="addTask">
		<p v-for="(item,index) in tasks">
			<input type="checkbox" @click="item.checked = !item.checked">
			<span :class="{isfinish: item.checked }">{{ item.content }}</span>
			<button @click="remove(index)">删除</button>
		</p>
	</div>
</template>

<script>
export default{
	props:['toutou'], //接受toutou内容
	created(){
		if(localStorage.getItem('tasks')){//如果本地有才加载
		var data = localStorage.getItem('tasks')//提取本地数据
		this.tasks = JSON.parse(data)//转化字符串为数组
		}
	},
	data () {
		return{
			tasks:[]
		}
	},
	watch: {
		tasks (newTas){
			console.log(newTas.length)
		}
	},

	methods :{
		addTask(e) {
			var task = {
				content: e.target.value,//复制表单值给content
				checked : false//默认不选中
			}
			this.tasks.push(task)//
			e.target.value=''//清空表单
			var data = JSON.stringify(this.tasks)//转化为字符串
			localStorage.setItem('tasks',data)//存在本地
			this.$emit('hi',this.tasks)//处罚hi事件，向父级页面传参this.tasks数组
		},
		remove(index){
			this.tasks.splice(index,1)
			var data = JSON.stringify(this.tasks)//转化为字符串
			localStorage.setItem('tasks',data)//存在本地
		}
	}
}
</script>

<style scoped>
	.isfinish{
		text-decoration: line-through;
		color: #ccc;
	}
</style>


